.header {
    .container {
        display: flex;
        flex-flow: row wrap;
    }
    .menu-burger {
        &__icon > .soc-media__icon {
            font-size: $quote;
            color: #4bc7c7;
            cursor: pointer;
            &:hover {
                color: $white;
                transition: all 0.3s ease;
            }
        }
    }
    #menu-checkbox {
        display: none;
    }
    #menu-checkbox:checked + .menu {
        display: block;
    }
    .left-part {
        position: relative;
        padding: rem(35) rem(25);
        background-color: $yellow;
        flex-basis: 50%;
        max-width: 50%;
        .menu {
            display: none;
            position: absolute;
            top: 0;
            left: 0;
            background-color: $dark-blue;
            width: 100%;
            height: 100%;
            .inner {
                padding: rem(42) rem(30);
                display: flex;
                flex-flow: row wrap;
                justify-content: space-between;
                .close {
                    color: $yellow;
                    font-size: rem(18);
                    cursor: pointer;
                    &:hover {
                        color: $light-yellow;
                    }
                }
            }
            &__items {
                display: flex;
                flex-flow: row wrap;
                padding-right: rem(100);
            }
            &__item {
                padding: 0 rem(10);
                border-right: 1px solid $yellow;
                &:last-child {
                    border: none;
                }
            }
            &__link {
                color: $yellow;
                text-transform: uppercase;
                &:hover {
                    color: $light-yellow;
                }
            }
        }
        .inner {
            padding: rem(60) rem(75);
            padding-bottom: rem(15);
            .content {
                display: flex;
                flex-direction: column;
                margin-bottom: rem(135);
                .logo {
                    margin-bottom: 10px;
                }
                .title {
                    white-space: pre-line;
                    color: $light-blue;
                    &::first-line {
                        color: $blue;
                    }
                }
            }
            .media {
                display: flex;
                flex-flow: row wrap;
                justify-content: space-between;
                .soc-media {
                    display: flex;
                    flex-basis: 35%;
                    max-width: 35%;
                    justify-content: space-between;
                    &__icon {
                        color: $light-blue;
                        &:hover {
                            color: $white;
                            transition: all 0.3s ease;
                        }
                    }
                }
                .contact {
                    display: flex;
                    flex-basis: 30%;
                    max-width: 30%;
                    justify-content: space-between;
                    .phone {
                        font-family: $font-lekton-regular;
                        color: $light-blue;
                        &:hover {
                            color: $white;
                        }
                    }
                }
            }
        }
    }
    .right-part {
        background-image: linear-gradient(90deg, #dfd8c6 0%, #d2cab7 100%);
        flex-basis: 50%;
        max-width: 50%;
        padding: rem(120) rem(215) rem(50) rem(215);
        .content {
            display: flex;
            flex-flow: column wrap;
            font-family: $font-lekton-regular;
            text-transform: uppercase;
            .details {
                span {
                    color: $light-yellow;
                }
                .nav-text {
                    color: $white;
                    line-height: 20px;
                }
            }
            .btn__wrapper {
                margin-top: rem(15);
                .btn {
                    &:hover {
                        background-color: $black;
                        transition: all 0.3s ease;
                    }
                    &:active {
                        background-color: $white;
                        color: $black;
                    }
                }
            }
        }
    }
}
@media screen and (max-width: 1200px) {
    .header {
        .right-part {
            padding: rem(80) rem(155) rem(20) rem(155);
        }
        .left-part {
            .inner {
                padding: rem(25) rem(15);
                .content {
                    margin-bottom: rem(90);
                }
            }
        }
    }
}
@media screen and (max-width: 960px) {
    .header {
        .left-part,
        .right-part {
            flex-basis: 100%;
            max-width: 100%;
        }
        .left-part {
            .inner {
                text-align: center;
            }
        }
        .right-part {
            align-content: center;
            padding: rem(50) rem(10);
            .content {
                text-align: center;
                flex-basis: 50%;
                max-width: 50%;
                .nav-text {
                    text-align: left;
                }
            }
        }
    }
}
