//
// FOOTER
//
.footer {
    .inner {
        display: flex;
        flex-flow: row wrap;
        .contact-form {
            flex-basis: 50%;
            max-width: 50%;
            padding: rem(50) rem(85);
            background-image: linear-gradient(90deg, #dfd8c6 0%, #d2cab7 100%);
            font-family: $font-lekton-regular;
            .quote {
                text-transform: uppercase;
                color: #646250;
            }
            .text {
                font-size: rem(12);
                text-transform: uppercase;
                line-height: 14px;
                color: #646250;
            }
            .form {
                padding: rem(45) rem(50) 0 rem(50);
                display: flex;
                flex-flow: column wrap;
                &__input {
                    padding: rem(5) 0;
                    color: #a6a385;
                    border: none;
                    border-bottom: 2px solid #a6a385;
                    text-transform: uppercase;
                    background-color: transparent;
                    margin-bottom: rem(30);
                }
                .btn {
                    padding: rem(10) rem(45);
                    &:hover {
                        background-color: $white;
                        color: $dark-gray;
                    }
                    &__wrapper {
                        padding-top: rem(20);
                    }
                }
            }
        }
        .contacts {
            display: flex;
            flex-direction: column;
            background-color: $yellow;
            padding: rem(115) rem(115) rem(25) rem(115);
            flex-basis: 50%;
            max-width: 50%;
            .content {
                display: flex;
                flex-flow: row wrap;
                justify-content: space-between;
                margin-bottom: rem(80);
                .company-name {
                    flex-basis: 50%;
                    max-width: 50%;
                    .title {
                        font-size: rem(24);
                        color: $blue;
                    }
                }
                .address {
                    display: flex;
                    flex-flow: column wrap;
                    font-family: $font-lekton-regular;
                    flex-basis: 30%;
                    max-width: 30%;
                    .text {
                        white-space: pre-line;
                        font-size: rem(13);
                        line-height: 14px;
                        color: #333;
                        text-transform: uppercase;
                        &:first-child {
                            margin-bottom: rem(15);
                        }
                    }
                    .mail {
                        color: $dark-blue;
                        &:hover {
                            color: $white;
                            transition: all 0.3s ease;
                        }
                    }
                }
            }
            .media {
                display: flex;
                flex-flow: row wrap;
                justify-content: center;
                margin-bottom: rem(100);
                .soc-media {
                    display: flex;
                    flex-basis: 35%;
                    max-width: 35%;
                    justify-content: space-between;
                    &__icon {
                        color: $light-blue;
                        &:hover {
                            color: $white;
                            transition: all 0.3s ease;
                        }
                    }
                }
            }
            .copyright {
                width: 63%;
                margin: 0 auto;
                text-align: center;
                .text {
                    font-family: $font-lekton-regular;
                    font-size: rem(13);
                    color: $black;
                    line-height: 14px;
                    margin-bottom: rem(14);
                    font-weight: 600;
                    &:last-child {
                        margin-bottom: 0;
                    }
                }
            }
        }
    }
}
@media screen and (max-width: 1200px) {
    .footer {
        .inner {
            .contact-form {
                padding: rem(50);
            }
            .contacts {
                padding: rem(50);
            }
        }
    }
}
@media screen and (max-width: 960px) {
    .footer {
        .inner {
            .contact-form,
            .contacts {
                flex-basis: 100%;
                max-width: 100%;
                text-align: center;
            }
            .contacts {
                .content,
                .media {
                    margin-bottom: rem(30);
                }
            }
        }
    }
}
